    private async initSource(): Promise<void> {
        this.formDataOptions.sourceIds = [];

        try {
            let data = this.Source;
            let status = 200;

            if (status === 200 && data) {
                this.formDataOptions.sourceIds = this.resolveSelectionGroup(data?.content);
                this.formDataOptions.sourceIdsAll = data?.content ?? [];
            }
        } catch (e) {
            console.error(e);
        }

        (this.formDataOptions.sourceIds || []).forEach((device: ISelectionOptionGroupSource) => {
            const type: string = this.$i18n[`Action_Video_Sources_${device.type}`];
            device.type = type;
        });
    }

    private async initDevice(): Promise<void> {
        this.formDataOptions.devices = [];

        try {
            let data = this.Device;
            let status = 200;

            if (status === 200) {
                this.formDataOptions.devices = this.resolveSelectionGroup(data);
                this.formDataOptions.devicesAll = data;
            }
        } catch (e) {
            console.error(e);
        }

        (this.formDataOptions.devices || []).forEach((device: ISelectionOptionGroupDevice) => {
            const type: string = this.$i18n[`Action_Device_${device.type}`];
            device.type = type;
        });
    }

