    private formDataSet(data: any) {
        // modify 用
        this.formData.label = data.label ? data.label : '';
        this.formData.color = data.color ? data.color : '#ffffff';
        this.formData.colorNo = data.color ? data.colorNo : '#ffffff';

        this.formData.decimal = data.decimal ? data.decimal : 0;
        this.formData.integer = data.integer ? data.integer : 0;
        this.formData.range = data.range ? data.range : 10;

        this.formData.account = data.account ? data.account : '';
        this.formData.name = data.name ? data.name : data.name;

        this.formData.role = data.role;
        this.formData.company = data.company;
        this.formData.building = data.build;
        this.formData.floor = data.floor;

        this.formData.email = data.email ? data.email : data.email;
        this.formData.phone = data.phone ? data.phone : data.phone;
        this.formData.remark = data.remark ? data.remark : data.remark;

        this.formData.protocol = this.formDataOptions.protocol.filter((item) => item.objectId === data.protocol)[0];
        this.formData.ip = data.ip ? data.ip : '';
        this.formData.port = data.port ? data.port : '';
        this.formData.ssl = data.ssl;

        this.formData.year = new Date(data.year);
        this.formData.month = new Date(data.month);
        this.formData.week = new Date(data.week);
        this.formData.date = new Date(data.date);
        this.formData.datetime = new Date(data.datetime);
        this.formData.time = new Date(data.time);
    }

