    private resolveSelectionGroup(allData: IDevice[] | ISource[]): ISelectionOptionGroupSource[] | ISelectionOptionGroupDevice[] {
        /*
        options: [
        {
          type: 'tablet',
          libs: [
            { objectId: 'Vue.js', name: 'Front-end', type: tablet },
            { objectId: 'Vue.js', name: 'Front-end', type: tablet },
          ]
        },
              ],
        */

        let _tempTypes: ISelectionOptionGroupSource[] | ISelectionOptionGroupDevice[] = [];
        (allData || []).forEach((item) => {
            _tempTypes.push({ type: item.type, libs: [] });
        });

        let tempTypes: Object = {};
        _tempTypes = _tempTypes.reduce((item, next) => {
            tempTypes[next.type] ? '' : (tempTypes[next.type] = true && item.push(next));
            return item;
        }, []);

        (allData || []).forEach((item) => {
            _tempTypes.forEach((type) => {
                if (type.type === item.type) {
                    type.libs.push({ objectId: item.objectId, name: item.name, type: type.type });
                }
            });
        });

        return _tempTypes;
    }

